        :tabOpts="item.options"
      ></ppt-table>
      <ppt-text
        :key="index"
        v-if="item.type == 'text'"
        :options="item.options"
      ></ppt-text>
      <ppt-text-arr
        :key="index"
        v-if="item.type == 'textArr'"
        :textArr="item.arr"
        :options="item.options"
      ></ppt-text-arr>
      <ppt-picture
        :key="index"
        v-if="item.type == 'picture'"
        v-model="item.options.path"
        :num="index"
        :options="item.options"
      ></ppt-picture>
    </template>
  </div>
</template>

<script>
import pptText from "@/components/Text";
import pptTable from "@/components/Table";
import pptTextArr from "@/components/TextArr";
import pptPicture from "@/components/picture";
export default {
  name: "pptCanvas",
  components: {
    pptText,
    pptTable,
    pptTextArr,
    pptPicture
  },
  props: {
    canvas: Array,
  },
};
</script>

