<template>
  <div>
    <div
      class="text-arr"
      :style="
        `lineHeight:${options.paraSpaceBefore+20}px;top:${options.y};left:${options.x};width:${options.w};height:${options.h};color:${options.color};font-size:${options.fontSize}px;`
      "
    >
      <template v-for="(item,index) in textArr">
        <span :key="`icon-${index}`" class="iconfont icon" v-if="item.options.bullet">&#xe687;</span>
        <p
          class="text"
          :key="index"
          :style="
            `font-size:${item.options.fontSize}px;color:#${item.options.color};text-decoration:${item.options.underline?'underline':'none'};`
          "
        >
          <edit-div
            v-model="item.text"
            :canEdit="!item.options.readonly"
          ></edit-div>
        </p>
        <br :key="`br-${index}`" v-if="item.options.break" />
      </template>
    </div>
  </div>
</template>

<script>
import editDiv from "@/components/editDiv";
export default {
  name: "pptTextArr",
  props: {
    textArr: Array,
    options: Object,
  },
  components: {
    editDiv,
  },
};
</script>

