<template>
  <div>
    <label :for="`input_${num}`">
      <img
        :src="src"
        class="img"
        :style="
          `width:${(options.w * 100) / 13.3}%;height:${(options.h * 100) /
            7.5}%;top:${(options.y * 100) / 7.5}%;left:${(options.x * 100) /
            13.3}%;`
        "
      />
    </label>
    <input type="file" name="pic" :id="`input_${num}`" class="input" @change="change" />
  </div>
</template>

<script>
export default {
  name: "pptPicture",
  props: {
    options: Object,
    num: Number,
    src: {
      type: String,
      default: "",
    },
  },
  model: {
    prop: "src",
    event: "change",
  },
  methods: {
    change(e) {
      let files = e.target.files[0];
      const src = this.getObjectUrl(files);
      this.$emit("change", src);
    },
    getObjectUrl(file) {
      let url = null;
      if (window.createObjectURL != undefined) {
        // basic
        url = window.createObjectURL(file);
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      } else if (window.URL != undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file);
      }
      return url;
    },
  },
  mounted() {
    console.log(this.src);
  },
};
</script>

<style scoped>
.img {
  position: absolute;
}
.input {
  display: none;
}
</style>
