        <span class="num">{{ index + 1 }}</span>
        <div class="pic">
          <span>这里以后是缩略图</span>
        </div>
      </li>
    </ul>
    <div class="project">
      <div class="canvas">
        <template v-for="(item, index) in pptData">
          <ppt-canvas
            :key="index"
            :canvas="item"
            v-if="index == page"
          ></ppt-canvas>
        </template>
      </div>
      <div class="button-box">
        <el-button type="primary" @click="printPPT">导出ppt</el-button>
        <el-button type="primary" @click="dialogFormVisible = true">数据配置</el-button>
      </div>
    </div>
    <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="项目" :label-width="formLabelWidth">
          <el-select v-model="form.project" placeholder="请选择">
            <el-option label="模拟数据一" :value="pptData_1"></el-option>
            <el-option label="模拟数据二" :value="pptData_2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="地块" :label-width="formLabelWidth">
          <el-select v-model="form.region" placeholder="请选择">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分期" :label-width="formLabelWidth">
          <el-select v-model="form.stages" placeholder="请选择">
            <el-option label="一" value="shanghai"></el-option>
            <el-option label="二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="三回版本" :label-width="formLabelWidth">
