<script>
import pptxgen from "pptxgenjs";
import pptCanvas from "@/components/pptCanvas";
import pptData from "@/assets/datas/obj_1.js";
import pptData_2 from "@/assets/datas/obj_2.js";
export default {
  name: "Home",
  components: {
    pptCanvas,
  },
  data() {
    //13.3 x 7.5英寸
    return {
      pptData: pptData.data,
      pptData_1: JSON.stringify(pptData),
      pptData_2: JSON.stringify(pptData_2),
      page: 0,
      dialogFormVisible: false,
      form: {
          project: '',
          region: '',
          stages: '',
          edition: ''
        },
        formLabelWidth: '120px'
    };
  },
  methods: {
    printPPT() {
      let pptx = new pptxgen();
      pptx.layout = "LAYOUT_WIDE";
      pptx.defineSlideMaster({
        title: "MASTER_SLIDE",
        bkgd: "FFFFFF",
        objects: [
          { rect: { x: 0.0, y: 0, w: "100%", h: 1.1, fill: "005FA2" } },
          // {
          //   image: {
          //     x: 0.0,
          //     y: 0,
          //     w: "100%",
          //     h: 1.1,
          //     path: require("../assets/image/ppt_master_title_bgi.png"),
          //   },
          // },
          {
            text: {
              text: "中南高科·服务中国制造业的超级平台",
              options: {
                x: 9.0,
                y: 0.6,
                fontSize: 16,
                color: "FFFFFF",
                fontFace: "Microsoft yahei",
              },
            },
          },
        ],
      });
      const pptData = JSON.parse(JSON.stringify(this.pptData));
      for (let page of pptData) {
        let slide = pptx.addSlide({ masterName: "MASTER_SLIDE" });
        for (let member of page) {
          if (member.type == "table")
            slide.addTable(member.data, member.options);
          if (member.type == "text")
            slide.addText(member.options.text, member.options);
          if (member.type == "textArr")
            slide.addText(member.arr, member.options);
          if (member.type == "picture") slide.addImage(member.options);
        }
      }
      pptx.writeFile("Demo-Tables");
    },
    /**
     * 点击切换幻灯片
     */
    chousePage(page) {
      this.page = page;
    },
    /**
     * 更新数据
     */
    updata() {
      this.dialogFormVisible = false;
      const {project} = this.form;
      // console.log(project);
      if(project){
        this.pptData = JSON.parse(project).data;
        this.page = 0;
      }
    },
    /**
     *
     * @param url 图片路径
     * @param ext 图片格式
     * @returns promise
     */
    // getUrlBase64(url, ext) {
    //   return new Promise((resolve) => {
    //     var canvas = document.createElement("canvas"); //创建canvas DOM元素
    //     var ctx = canvas.getContext("2d");
    //     var img = new Image();
    //     img.crossOrigin = "Anonymous";
    //     img.src = url;
    //     console.log(img);
    //     img.onload = function() {
    //       console.log(this);
    //       canvas.height = 60; //指定画板的高度,自定义
    //       canvas.width = 85; //指定画板的宽度，自定义
    //       ctx.drawImage(img, 0, 0, 60, 85); //参数可自定义
    //       var dataURL = canvas.toDataURL("image/" + ext);
    //       canvas = null;
    //       resolve( dataURL); //回掉函数获取Base64编码
    //     };
    //   });
    // },
  },
  mounted() {
    console.log(pptData,pptData_2);
    // this.getUrlBase64(require("../assets/image/ppt_master_title_bgi.png"),"png").then((dataURL)=>{
    //   console.info(dataURL);
    // })
  },
};
