<template>
  <div>
    <table :width="`${tabOpts.w*10}%`" class="table" :style="`width:${tabOpts.w*100/13.3}%;top:${tabOpts.y*100/7.5}%;left: ${tabOpts.x*100/13.3}%;`">
      <colgroup>
        <col v-for="(item,index) in tabOpts.colW" :key="index" :style="`width:${item*10}%;`">
      </colgroup>
      <tbody>
        <tr class="tr" v-for="(tr, trindex) in rows" :key="trindex">
          <template v-for="(td, tdindex) in tr">
            <td
              v-if="!td.options.hide"
              class="td"
              :key="tdindex"
              :colspan="td.options.colspan"
              :rowspan="td.options.rowspan"
              :style="
                `background-color:#${td.options.fill};color:#${
                  td.options.color
                };font-size:${
                  td.options.fontSize ? td.options.fontSize : tabOpts.fontSize
                }px;`
              "
            >
              <editDiv
                v-model="td.text"
                :canEdit="!td.options.readonly"
                :options="td.options"
              ></editDiv>
            </td>
          </template>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import editDiv from "@/components/editDiv";
export default {
  name: "pptTable",
  components: {
    editDiv,
  },
  props: {
    rows: Array,
    tabOpts: Object,
  },
  data() {
    return {};
  },
};
</script>

<style scoped>
.table,
tr,
.td {
  border: 0.5pt solid #333000;
  text-align: center;
  border-collapse: collapse;
  font-size: 12px;
}
.table {
  position: absolute;
  table-layout: fixed;
  word-wrap: break-word;
}
</style>
